/* Typography
============================================================================= */
:where(.typography) {
  font-size: clamp(16px, 14.6111111111px + 0.2777777778vw, 18px);
  line-height: 1.6666666667;
  letter-spacing: -0.5px;
}
:where(.typography) > :first-child {
  margin-top: 0;
}
:where(.typography) > :only-child {
  margin-block: 0;
}
:where(.typography) a {
  text-decoration: underline;
  transition: color 0.25s;
  letter-spacing: -0.5px;
  color: #1e1e64;
}
:where(.typography) a:hover, :where(.typography) a:focus {
  color: #000;
}
:where(.typography) p {
  margin: 30px 0;
