:where(.typography) h2 + *,
:where(.typography) h3 + *,
:where(.typography) h4 + *,
:where(.typography) h5 + *,
:where(.typography) h6 + * {
  margin-top: 0;
}
:where(.typography) h1 {
  font-size: clamp(38px, 8.8333333333px + 5.8333333333vw, 80px);
  font-weight: 800;
  line-height: 1.05;
  letter-spacing: -0.5px;
  margin-block: 50px 10px;
}
:where(.typography) h2 {
  font-size: clamp(32px, 16.7222222222px + 3.0555555556vw, 54px);
  font-weight: 800;
  line-height: 1.2222222222;
  letter-spacing: -0.5px;
  margin-block: 50px 10px;
}
:where(.typography) h3 {
  font-size: clamp(28px, 18.2777777778px + 1.9444444444vw, 42px);
  font-weight: 800;
  line-height: 1.2380952381;
  letter-spacing: -0.5px;
  margin-block: 50px 10px;
}
:where(.typography) h4 {
  font-size: clamp(24px, 18.4444444444px + 1.1111111111vw, 32px);
  font-weight: 700;
  line-height: 1.3125;
  letter-spacing: -0.5px;
  margin-block: 50px 10px;
}
:where(.typography) h5 {
  font-size: clamp(18px, 13.8333333333px + 0.8333333333vw, 24px);
  font-weight: 700;
  line-height: 1.3333333333;
  margin-block: 50px 10px;
}
:where(.typography) h6 {
  font-size: 14px;
  font-weight: 500;
  letter-spacing: 1px;
  line-height: 1.2857142857;
  text-transform: uppercase;
  margin-block: 50px 10px;
