  letter-spacing: -0.5px;
  color: #000;
}
:where(.typography) .intro p {
  margin-block: 0;
}
:where(.typography) .copy {
  font-size: clamp(16px, 14.6111111111px + 0.2777777778vw, 18px);
  line-height: 1.6666666667;
  letter-spacing: -0.5px;
}
:where(.typography) .small {
  font-size: 16px;
  line-height: 1.5;
  letter-spacing: -0.5px;
}
:where(.typography) .label {
  font-size: 12px;
  line-height: 1.5714285714;
  letter-spacing: -0.5px;
}
:where(.typography) .caption {
  font-size: clamp(14px, 12.6111111111px + 0.2777777778vw, 16px);
  font-weight: 500;
  line-height: 1.5;
  letter-spacing: -0.5px;
}

