}
@media print {
  :where(.typography) .video_frame {
    display: none;
  }
}

/* ==========================================================================
	Buttons
============================================================================= */
button {
  background: none;
  border: none;
  cursor: pointer;
  padding: 0;
}

/* Skip Link
============================================================================= */
.skip_link {
  display: inline-block;
  cursor: pointer;
  padding: 20px 30px;
  transition: background-color 0.25s, color 0.25s;
  background-color: #eaeaea;
  color: #000;
  position: absolute;
  top: 0;
  left: 50%;
  z-index: 100;
  display: block;
  opacity: 0;
  padding: 10px 20px;
  text-align: center;
  translate: -50% -300%;
  transition: opacity 0.35s, transform 0.35s, visibility 0.35s;
}
.skip_link .icon {
  display: inline;
}
@media print {
  .skip_link {
    border: 1px solid;
  }
}
.skip_link:focus-visible {
  background-color: #000;
  color: #eaeaea;
}
@media (hover: hover) {
  .skip_link:hover {
    background-color: #000;
    color: #eaeaea;
  }
}
.skip_link [class$=label] {
  font-size: clamp(18px, 13.8333333333px + 0.8333333333vw, 24px);
  font-weight: 700;
  line-height: 1.3333333333;
}
.skip_link:focus, .skip_link:active {
  opacity: 1;
  translate: -50% 40px;
  visibility: visible;
}

